/* -- Presets & Resets -- */
/* -- Elements -- */
*,
* :before,
* :after {
  box-sizing: border-box;
  font-size: 100%;
  outline: none;
  resize: none;
  border: none;
  padding: 0;
  margin: 0;
}

html,
body {
  text-rendering: optimizeLegibility;
  font-family: "Roboto", sans-serif;
  background: #f5f8fc;
  line-height: 1.15;
  font-weight: 400;
  font-size: 15px;
  color: #222;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  line-height: 1.15;
  font-weight: 400;
  color: inherit;
  display: block;
}

h1,
.h1 {
  margin: 2.1rem 0 1.68rem 0;
  font-size: 4.2rem;
  font-weight: 900;
}

h2,
.h2 {
  margin: 1.78rem 0 1.424rem 0;
  font-size: 3.56rem;
  font-weight: 700;
}

h3,
.h3 {
  margin: 1.46rem 0 1.168rem 0;
  font-size: 2.92rem;
  font-weight: 600;
}

h4,
.h4 {
  margin: 1.14rem 0 0.912rem 0;
  font-size: 2.28rem;
}

h5,
.h5 {
  margin: 0.82rem 0 0.656rem 0;
  font-size: 1.64rem;
}

h6,
.h6 {
  margin: 0.5rem 0 0.4rem 0;
  font-size: 1rem;
}

p,
.p {
  margin: 0 2em 10px 2em;
}

img,
load-img {
  min-height: 200px;
  display: block;
  width: 100%;
}

load-img {
  animation: load 0.5s ease infinite alternate;
  will-change: background;
  background: #ddd;
  min-height: 300px;
}

@keyframes load {
  to {
    background: #bbb;
  }
}

a {
  transition: color 0.25s ease;
  text-decoration: none;
  display: inline-block;
  will-change: color;
  overflow: hidden;
  cursor: pointer;
  color: #0ccc00;
}

a :hover,
a :focus {
  text-decoration: underline;
}

ul,
ol {
  list-style-type: disc;
  margin: 20px 2em;
  display: block;
}

ol {
  list-style-type: decimal;
}

ul ul,
ol ul {
  list-style-type: circle;
}

li {
  text-align: inherit;
  display: list-item;
}

.list-none {
  list-style: none;
}

/* -- Header -- */
.header {
  position: relative;
  display: block;
  /* -- Navigation -- */
}

.header-top-spot {
  height: 60px;
}

.header .navbar {
  justify-content: flex-start;
  flex-direction: column;
  line-height: 60px;
  position: relative;
  flex-wrap: nowrap;
  height: 60px;
  flex-shrink: 0;
  z-index: 1200;
  width: 100%;
}

.header .navbar-layout {
  display: flex;
}

.header .navbar-fixed {
  position: fixed;
  left: 0;
  top: 0;
}

.header .navbar-head {
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  flex-shrink: 0;
  display: flex;
  left: 0;
  top: 0;
}

.header .navbar-title {
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.02em;
  text-decoration: none;
  position: relative;
  font-size: 17px;
  flex-shrink: 0;
  margin: 5px 0;
}

.header .navbar-list {
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  list-style: none;
  display: flex;
  margin: 0;
}

.header .navbar-link {
  font-family: "Varela Round", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
  padding: 0 28px;
  font-size: 14px;
  display: block;
  color: #000;
}

.header .navbar-link:hover {
  color: #ddd !important;
}

.header .navbar-link-focus {
  color: #bbb;
}

