/*
    -- Layer (represents a section of code that displays content) --
     * ".more" = is for the learn more buttons
*/
.layer {
  font-family: "Roboto", sans-serif;
  position: static;
  line-height: 1.85;
  font-size: 18px;
  display: block;
  width: 100%;
  z-index: 0;
}

.layer .layer-image {
  position: absolute;
  z-index: 0;
  left: 0;
  top: 0;
}

.layer .layer-image-overlay {
  background: #222;
  background: #252b33;
  position: absolute;
  display: block;
  height: 100%;
  opacity: 0.35;
  width: 100%;
  z-index: 5;
  left: 0;
  top: 0;
}

.layer .layer-image-blur {
  filter: blur(3px);
}

.layer-shadow,
.layer .layer-hero {
  box-shadow: 0 20px 85px rgba(0, 0, 40, 0.3);
}

.layer-shadow--1 {
  box-shadow: 0 20px 65px rgba(0, 0, 40, 0.12);
}

.layer-hero {
  background: #fff;
  border-radius: 25px;
  position: relative;
  max-height: 700px;
  overflow: hidden;
  z-index: 125;
}

.layer-hero .layer-content {
  position: relative;
  z-index: 25;
}

.layer-hero-scroll-down {
  background: #fff;
  position: relative;
  margin-bottom: -150px;
  z-index: 250;
}

.layer-hero-scroll-down :hover {
  color: #2d22ca !important;
}

